<template>
  <md-layout>
    <h1 class="md-headline">
      {{sections.portfolio.heading}}
      <br/>
      <span class="md-caption" v-html="sections.portfolio.subheading"></span>
    </h1>
    <md-whiteframe md-elevation="0" class="full-width">
      <carousel-3d>
        <slide v-for="(item, index) in portfolio" :key="item.title" :index="index">
          <md-card>
            <md-card-header>
              <md-card-header-text>
                <div class="md-title">{{item.title}}</div>
                <div class="md-subhead">{{item.tags}}</div>
              </md-card-header-text>
            </md-card-header>
  
            <md-card-media>
              <!--<img :src="pathToImgs(item.images[0], true)" alt="People">-->
            </md-card-media>
  
            <md-card-content>
              <div class="md-title">{{item.summary}}</div>
            </md-card-content>
          </md-card>
        </slide>
      </carousel-3d>
    </md-whiteframe>
    <br/>
  </md-layout>
</template>

<script>
  import { mapGetters } from 'vuex'

  // const pathToImgs = require.context('~assets/img/backgrounds/', true)

  export default {
    computed: {
      ...mapGetters(['myInfo', 'sections', 'portfolio'])
    },
    mounted () {
      // console.log(pathToImgs)
    }
  }
</script>

<style>
  
</style>



